@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
#intro_actividad03 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  position: relative;
  background-image: url("/build/img/contenido/Pag04/fondo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 768px) {
  #intro_actividad03 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
  }
}
#intro_actividad03::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #129a63;
  z-index: -1;
}
#intro_actividad03 .cont-vineta {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
#intro_actividad03 .cont-vineta #vineta03 {
  margin-top: 6rem;
  border: 5px solid #3b5895;
  background: #ffffff;
  width: 90%;
  padding: 2rem;
  border-radius: 2rem;
  line-height: 1.6;
  position: relative;
}
@media (max-width: 768px) {
  #intro_actividad03 .cont-vineta #vineta03 {
    margin-top: 1rem;
  }
}
#intro_actividad03 .cont-vineta #vineta03::before {
  content: "";
  position: absolute;
  top: 20%;
  left: calc(100% + 0.4rem);
  width: 3rem;
  height: 4rem;
  background: #3b5895;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media (max-width: 768px) {
  #intro_actividad03 .cont-vineta #vineta03::before {
    top: calc(100% + 0.4rem);
    left: 40%;
    width: 4rem;
    height: 3rem;
    clip-path: polygon(0 0, 100% 0%, 50% 100%);
  }
}
#intro_actividad03 .cont-vineta #vineta03 #frase_01 {
  color: #3b5895;
  font-weight: 700;
}
#intro_actividad03 .cont-vineta #vineta03 #frase_02 {
  color: #129a63;
  font-weight: 700;
}
#intro_actividad03 .cont-imagen {
  min-height: 100%;
}

#zona_actividad01 {
  width: 100%;
  padding: 10rem 2rem 2rem 2rem;
  background-image: url(/build/img/contenido/Pag03/fondo.svg);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
}
#zona_actividad01 #pines {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
#zona_actividad01 #pines .definiciones {
  width: 10rem;
  height: 4.5rem;
  position: relative;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 3.2rem;
  box-shadow: 10px 11px 5px -4px rgba(0, 0, 0, 0.75);
  transition: animation 0.3s ease-in-out;
  z-index: 12;
}
#zona_actividad01 #pines .definiciones:hover {
  cursor: pointer;
  animation: 1s balanceo 1;
}
#zona_actividad01 #pines .definiciones p {
  font-size: 1rem;
  text-align: center;
  font-weight: 700;
  padding-bottom: 0.5rem;
}
#zona_actividad01 #pines .definiciones .pin {
  position: absolute;
  z-index: 11;
  width: 3.2rem;
  height: 3.2rem;
  top: -1.2rem;
  left: 30%;
}
#zona_actividad01 #pines .definiciones::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1rem;
  top: 0;
  left: 0;
}
#zona_actividad01 #pines .definiciones#definicion01::before {
  background: #e25327;
}
#zona_actividad01 #pines .definiciones#definicion02::before {
  background: #3d3d3d;
}
#zona_actividad01 #pines .definiciones#definicion03::before {
  background: #2c4268;
}
#zona_actividad01 #pines .definiciones#definicion04::before {
  background: #f9c930;
}
#zona_actividad01 #pines .definiciones#definicion05::before {
  background: #2c4268;
}
#zona_actividad01 #pines .definiciones#definicion06::before {
  background: #f9c930;
}
#zona_actividad01 #pines .definiciones#definicion07::before {
  background: #616a84;
}
#zona_actividad01 #pines .definiciones#definicion08::before {
  background: #e25327;
}
#zona_actividad01 #post-its {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
#zona_actividad01 #post-its .posts {
  width: 25rem;
  height: 15rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 1.6rem;
  font-size: 1.2rem;
  font-weight: 700;
  color: #3b5895;
  margin-bottom: 3rem;
  position: relative;
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
#zona_actividad01 #post-its .posts .clip {
  position: absolute;
  width: 3rem;
  height: 5rem;
  top: -0.5rem;
  right: -1rem;
  z-index: 4;
}
#zona_actividad01 #post-its .posts p {
  margin-top: 2rem;
}
#zona_actividad01 #post-its .posts#post01 {
  background-image: url(/build/img/contenido/Pag03/post-it1.svg);
}
#zona_actividad01 #post-its .posts#post02 {
  background-image: url(/build/img/contenido/Pag03/post-it2.svg);
}
#zona_actividad01 #post-its .posts#post03 {
  background-image: url(/build/img/contenido/Pag03/post-it3.svg);
}
#zona_actividad01 #post-its .posts#post04 {
  background-image: url(/build/img/contenido/Pag03/post-it4.svg);
}
#zona_actividad01 #post-its .posts#post05 {
  background-image: url(/build/img/contenido/Pag03/post-it5.svg);
}
#zona_actividad01 #post-its .posts#post06 {
  background-image: url(/build/img/contenido/Pag03/post-it6.svg);
}
#zona_actividad01 #post-its .posts#post07 {
  font-size: 1rem;
  background-image: url(/build/img/contenido/Pag03/post-it7.svg);
}
#zona_actividad01 #post-its .posts#post08 {
  background-image: url(/build/img/contenido/Pag03/post-it8.svg);
}

@keyframes balanceo {
  20% {
    transform: rotate(8deg);
  }
  40%, 80% {
    transform: rotate(-8deg);
  }
  60%, 95% {
    transform: rotate(8deg);
  }
}